import { Component, OnInit, Input, OnDestroy, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { BaseView } from '../cee-base-class';
import { UserDataHandlerService } from '../../services/user-data-handler.service';
import { WfeStepLoaderService } from '../../services/wfe-step-loader.service';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';

import { AppDataService } from '../../models/app-data/app-data.service';
import { ApiDataService } from '../../models/api-data/api-data.service';
import { MatDialog as MatDialog } from '@angular/material/dialog';
import { MatSnackBar as MatSnackBar } from '@angular/material/snack-bar';
import { HttpClient } from '@angular/common/http';
import { CeeApiService } from '../../services/cee-api-service.service';
import { CEEInternalEmitterService } from '../../services/internal-cee-emitter-service.service';
import { CommonModule, NgStyle } from '@angular/common';


@Component({
    selector: 'app-cee-link',
    templateUrl: './cee-link.component.html',
    styleUrls: ['./cee-link.component.scss'],
    standalone: true,
    imports: [NgStyle, CommonModule]
})
export class CeeLinkComponent extends BaseView implements OnInit, OnDestroy {

    @Input() declare stepId: any;
    @Input() declare fieldData: any;
    @Input() primaryKeyIndex: any;
    @Input() keepState = false;
    @Input() rowData: any;
    @Input() declare rootData: any;

    Target: string;
    constructor(
        public router: Router,
        public userDataHandlerService: UserDataHandlerService,
        public sharedEventsService: SharedEventsServiceService,
        public internalCEEEmitter: CEEInternalEmitterService,
        public appDataService: AppDataService,
        public apiDataService: ApiDataService,
        public wfeStepLoaderService: WfeStepLoaderService,
        public dialog: MatDialog,
        public snackBar: MatSnackBar,
        public http: HttpClient,
        public ceeApiService: CeeApiService
    ) {
        super(userDataHandlerService,
            sharedEventsService,
            internalCEEEmitter,
            appDataService, apiDataService, wfeStepLoaderService, dialog, snackBar, http, ceeApiService, router);
    }

    ngOnInit() {
        if (!this.fieldData.link_config) {
            this.fieldData.link_config = [{}];
        }
        else if (!Array.isArray(this.fieldData.link_config) && typeof (this.fieldData.link_config) === 'object') {
            this.fieldData.link_config = [this.fieldData.link_config];
        }
        this.init(this.stepId, this.fieldData, this.keepState, this.rowData);
    }

    //@HostListener('window:beforeunload')
    onDestroy() {
        this.onViewUnload();
    }

    ngOnDestroy() {
        this.onViewUnload();
    }

    onViewDataInit(): void {
        if (this.rowData && this.rowData.hasOwnProperty('value')) {
            this.fieldData = this.setDynamicLabelUtil.getDynamicValue(this.fieldData, this.rowData.value);
            this.fieldData.link_config.forEach(config => {
                this.setApiData(config, this.rowData.value);
            });
        }

        this.fieldData.link_config.forEach(config => {
            if (!config.link_name && !this.fieldData.field_label.includes('((dynamic))')) {
                config.link_name = this.fieldData.field_label
            }
        });

        if (this.additionalParameter['Target']) {
            switch (this.additionalParameter['Target']) {
                case 'Blank':
                    this.Target = '_blank';
                    break;
                case 'Top':
                    this.Target = '_top';
                    break;
                default:
                    break;
            }
        }
    }

    onAPICallback(data: any): void {
        this.fieldData = this.setDynamicLabelUtil.getDynamicValue(this.fieldData, data);
        this.fieldData.link_config.forEach(config => {
            this.setApiData(config, data);
            if (!config.link_name && !this.fieldData.field_label.includes('((dynamic))')) {
                config.link_name = this.fieldData.field_label
            }
        });
    }
    private setApiData(config, data) {
        if (config.link_name_api_key) {
            const link_name = this.setApiCallBackDataUtil.setApiCallBackData(config.link_name_api_key, data);
            if (link_name) {
                config.link_name = link_name;
            }
        }
        if (config.link_api_key) {
            const link = this.setApiCallBackDataUtil.setApiCallBackData(config.link_api_key, data);
            if (link) {
                config.link = link;
            }
        }
    }

    onEmptySession(data: any) {
        //throw new Error('Method not implemented.');
    }
    handleCustomFunction(get_data: any) {
        //throw new Error('Method not implemented.');
    }
    onSetSessionData(apiKey: string, value: any): void {

    }

    concatData() { }

    /*
     * special event handled for link types
    */
    switchEvent(event) {
        const ModalOnPageLeave = this.additionalParameter['ModalOnPageLeave'] ? this.additionalParameter['ModalOnPageLeave'] : null;
        switch ((event.link_type).toLowerCase()) {
            case 'step':
                this.wfeEventListHandler.redirectTo(event.link, ModalOnPageLeave, this.Target);
                break;
            case 'workflow':
                this.wfeEventListHandler.redirectTo(
                    this.wfeStepLoaderService.getSequenceDefaultStepIdBySequenceId(event.link), ModalOnPageLeave, this.Target);
                break;
            /* istanbul ignore next */
            case 'external_url':
                this.sharedEventsService.closeDrawerOnClickExternalUrl.emit(true);
                setTimeout(() => {
                    this.wfeEventListHandler.redirectToHref(event.link, ModalOnPageLeave, this.Target);
                }, 10);
                break;
        }
    }

    setFieldValueOnSetValuesEvent(data: object) {

    }

    setFieldStateOnStateChangeEvent(data: object) {

    }

    toggleLanguage(data: object) {

    }

    updateStateHistory(data: any): void {
        // throw new Error("Method not implemented.");
    }

    onContextMenu(event: MouseEvent) {
        if (event.button === 2) {
            this.fieldData?.event_list?.forEach(x =>{
                if(x.event_name === "OnRightClick" && x.isDefault === true){
                    event.preventDefault();
                }
            })
            let objectLoc = {
                x : event.clientX + 'px',
                y : event.clientY + 'px'
            }
            this.onComponentEvent('OnRightClick', objectLoc);
        }
    }
}
